import { Component, OnInit, Input, HostListener } from '@angular/core';
import { TranslateService } from 'src/app/services/translate/translate.service';
import { Router } from '@angular/router';
import { SharedService } from 'src/app/services/shared.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  
 

 
  constructor(private ts:TranslateService,private sh:SharedService,private router: Router) { }

  ngOnInit() {
    
  }
  getLang(data) {
    this.ts.updateComp1Val(data.target.value);
    window.sessionStorage.setItem('lang', data.target.value);
     
  }
  goToProducts(data)
  {
    this.sh.setValue(data);
    this.router.navigate(['products']);
  }
  myFunction()
  {
    var x = document.getElementById("myTopnav") as HTMLInputElement;
    var logo = document.getElementById("logo") as HTMLInputElement;
   if (x.className === "topnav") {
     x.className += " responsive";
     x.style.height = "auto";
     
     logo.style.display = "none";
   } else {
     x.className = "topnav";
     x.style.height = "7em";
     logo.style.display = "block";
     
   }
  }
  
  
}
