import { Component, OnInit } from '@angular/core';
import { TranslateService } from 'src/app/services/translate/translate.service';
import { SharedService } from 'src/app/services/shared.service';

import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';


@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss']
})
export class ProductsComponent implements OnInit {
  selectedValue: any;
  httpHeaders = new HttpHeaders({
    'Content-Type': 'application/json',
    'Cache-Control': 'no-cache'
  });


  options = {
    headers: this.httpHeaders
  };

  jsondata: any[]= [];
  value: any ='allproducts';
  tempdata: any[] = [];
  constructor(private ts:TranslateService,private sh:SharedService, private http:HttpClient) { }

  ngOnInit() {
    
    if(this.sh.getValue())
      this.value = this.sh.getValue();
    console.log(this.value);
    this.getAllProducts().subscribe(data => {
      this.jsondata = data;
      this.tempdata = this.jsondata;
      console.log(this.jsondata);
    });

    (document.getElementById(this.value) as HTMLInputElement).checked = true;

  }

  getAllProducts(): Observable<any> 
  {
    return this.http.get('./assets/products.json', this.options);
  }

  handleChange(event)
  {
   
    this.selectedValue = event;
    if (this.selectedValue == 'allproducts')
    {
      this.tempdata = this.jsondata;
    }
    else
    {
      this.tempdata = this.jsondata.filter(data => {
       return  data.category.toLowerCase() == this.selectedValue;
      });
      console.log(this.tempdata);
      }
  }

}
